<template>
	<view class="weightPage">
		<view class="header_box">
			<view class="page_title">
				体重管理
			</view>
			<view class="add_weight_btn" @click="showAddModal = true">
				<text>+ 记录体重</text>
			</view>
		</view>
		
		<view class="bmi_box">
			<view class="bmi_title">BMI指数</view>
			<view class="bmi_content">
				<view class="bmi_value">{{currentBMI}}</view>
				<view class="bmi_status" :class="bmiStatusClass">{{bmiStatus}}</view>
			</view>
			<view class="bmi_info">
				<text>身高: {{userInfo.height}}cm</text>
				<text>体重: {{userInfo.weight}}kg</text>
			</view>
		</view>
		
		<view class="weight_trend_box">
			<view class="trend_title">体重趋势</view>
			<view class="trend_chart">
				<qiun-data-charts
					type="line"
					:opts="chartOpts"
					:chartData="weightChartData"
				/>
			</view>
		</view>
		
		<view class="weight_records_box">
			<view class="records_title">体重记录</view>
			<view class="weight_records">
				<view class="weight_record" v-for="record in weightRecords" :key="record.id">
					<view class="record_date">{{record.date}}</view>
					<view class="record_weight">{{record.weight}}kg</view>
					<view class="record_bmi">BMI: {{record.bmi}}</view>
					<view class="record_change" :class="record.changeClass">
						{{record.change}}
					</view>
				</view>
			</view>
		</view>
		
		<!-- 添加体重记录弹窗 -->
		<view class="modal" v-if="showAddModal" @click="showAddModal = false">
			<view class="modal_content" @click.stop>
				<view class="modal_title">记录体重</view>
				<view class="form_item">
					<text class="form_label">体重(kg)</text>
					<input type="number" v-model="newWeight.weight" placeholder="请输入体重" step="0.1"/>
				</view>
				<view class="form_item">
					<text class="form_label">记录日期</text>
					<picker mode="date" :value="newWeight.date" @change="onDateChange">
						<view class="picker">{{newWeight.date}}</view>
					</picker>
				</view>
				<view class="form_item">
					<text class="form_label">备注</text>
					<input v-model="newWeight.note" placeholder="可选，如：运动后"/>
				</view>
				<view class="modal_actions">
					<button class="cancel_btn" @click="showAddModal = false">取消</button>
					<button class="confirm_btn" @click="addWeightRecord">确定</button>
				</view>
			</view>
		</view>
	
	</view>
</template>

<script>
export default {
	data() {
		return {
			showAddModal: false,
			userInfo: {
				height: 175,
				weight: 70
			},
			newWeight: {
				weight: '',
				date: '',
				note: ''
			},
			weightRecords: [
				{
					id: 1,
					date: '2024-01-15',
					weight: 70.2,
					bmi: 22.9,
					change: '+0.2kg',
					changeClass: 'increase'
				},
				{
					id: 2,
					date: '2024-01-10',
					weight: 70.0,
					bmi: 22.9,
					change: '-0.5kg',
					changeClass: 'decrease'
				},
				{
					id: 3,
					date: '2024-01-05',
					weight: 70.5,
					bmi: 23.0,
					change: '+0.3kg',
					changeClass: 'increase'
				}
			],
			chartOpts: {
				color: ["#4A89FF"],
				padding: [15, 15, 0, 15],
				enableScroll: true,
				legend: {
					show: false
				},
				xAxis: {
					disableGrid: true
				},
				yAxis: {
					gridType: "dash",
					dashLength: 2
				},
				extra: {
					line: {
						type: "curve",
						width: 2,
						activeType: "hollow"
					}
				}
			},
			weightChartData: {
				categories: ["01-05", "01-10", "01-15"],
				series: [
					{
						name: "体重",
						data: [70.5, 70.0, 70.2]
					}
				]
			}
		};
	},
	computed: {
		currentBMI() {
			const height = this.userInfo.height / 100;
			const weight = this.userInfo.weight;
			return (weight / (height * height)).toFixed(1);
		},
		bmiStatus() {
			const bmi = parseFloat(this.currentBMI);
			if (bmi < 18.5) return '偏瘦';
			if (bmi < 24) return '正常';
			if (bmi < 28) return '偏胖';
			return '肥胖';
		},
		bmiStatusClass() {
			const bmi = parseFloat(this.currentBMI);
			if (bmi < 18.5) return 'underweight';
			if (bmi < 24) return 'normal';
			if (bmi < 28) return 'overweight';
			return 'obese';
		}
	},
	methods: {
		onDateChange(e) {
			this.newWeight.date = e.detail.value;
		},
		addWeightRecord() {
			if (!this.newWeight.weight || !this.newWeight.date) {
				uni.showToast({
					title: '请填写完整信息',
					icon: 'none'
				});
				return;
			}
			
			const weight = parseFloat(this.newWeight.weight);
			const height = this.userInfo.height / 100;
			const bmi = (weight / (height * height)).toFixed(1);
			
			// 计算变化
			const lastRecord = this.weightRecords[0];
			const change = lastRecord ? (weight - lastRecord.weight).toFixed(1) : 0;
			const changeText = change > 0 ? `+${change}kg` : `${change}kg`;
			const changeClass = change > 0 ? 'increase' : change < 0 ? 'decrease' : 'stable';
			
			const record = {
				id: Date.now(),
				date: this.newWeight.date,
				weight: weight,
				bmi: parseFloat(bmi),
				change: changeText,
				changeClass: changeClass,
				note: this.newWeight.note
			};
			
			this.weightRecords.unshift(record);
			this.userInfo.weight = weight;
			this.updateChartData();
			this.showAddModal = false;
			this.newWeight = { weight: '', date: this.getCurrentDate(), note: '' };
			
			uni.showToast({
				title: '记录成功',
				icon: 'success'
			});
		},
		updateChartData() {
			// 更新图表数据
			const recentRecords = this.weightRecords.slice(0, 7).reverse();
			this.weightChartData = {
				categories: recentRecords.map(r => r.date.split('-').slice(1).join('-')),
				series: [
					{
						name: "体重",
						data: recentRecords.map(r => r.weight)
					}
				]
			};
		},
		getCurrentDate() {
			const now = new Date();
			return now.getFullYear() + '-' + 
				   (now.getMonth() + 1).toString().padStart(2, '0') + '-' + 
				   now.getDate().toString().padStart(2, '0');
		}
	},
	onLoad() {
		this.newWeight.date = this.getCurrentDate();
		this.updateChartData();
	}
};
</script>

